import React from 'react';
import {Link} from 'react-router-dom';
import logo from '../../Assets/images/HelloWorld4.png';

class Landingpage extends React.Component {
    render() {
        return (
            <div className="landingpage">
                <h1>"Hello, World!" Hackathon</h1>
                <img className="landingpage-logo" src={logo} alt="hackathon logo" />
                <div>
                    <Link  to="/signin" ><button type="button " className="button landing-btn primary-button upper-case">Signin</button></Link>
                    <Link  to="/signup" ><button type="button " className="button landing-btn primary-button upper-case">Signup</button></Link>
                </div>
            </div>
        )
    }
}

export default Landingpage; 