import React from 'react';
import SkillsList from './SkillsList';
import SearchBar from './SearchBar';
import {AllSkills} from './Skills';


class Learn extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            Skills: [],
        };
        this.searchAllSkills = this.searchAllSkills.bind(this);
    }

    searchAllSkills(sortBy) {
        let newArr = [];
        for(let i = 0; i < AllSkills.length; i++) {
            if(AllSkills[i].category === sortBy) {
                newArr.push(AllSkills[i]);
            }
            this.setState({ Skills: newArr });
        }
    }

    render() {
        return (
            <div className="Learn">
                <SearchBar searchAllSkills={this.searchAllSkills} />
                <SkillsList skills={this.state.Skills}/> 
            </div>
        );
    }
}

export default Learn;