import React from 'react';
import FunList from './FunList';
import image from "../../../Assets/images/HelloWorld4.png";


const FunItems = [
    {
        imageSrc: {image},
        name: 'FUN 1',
        address: "/htmlSkeleton",
        description: "FUN FUN FUN"
      },
    {
        imageSrc: {image},
        name: 'FUN 2',
        address: "/htmlHeadings",
        description: "FUN FUN FUN"
    },
    {
        imageSrc: {image},
        name: 'FUN 3',
        address: "/htmlImages",
        description: "FUN FUN FUN"
    },
    {
        imageSrc: {image},
        name: 'FUN 4',
        address: "/cssTags",
        description: "FUN FUN FUN"
    }
]

class Fun extends React.Component {
    render() {
        return (
            <div className="Fun">
                <FunList funItems={FunItems}/>
            </div>
        );
    }
}

export default Fun;