import React from 'react';
import FunStuff from './FunStuff';
import "../learnComponent/SkillsList.css";

class FunList extends React.Component {
    render() {
        return (
            <div className="SkillsList">
                {
                    this.props.funItems.map((funItem) => {
                        return <FunStuff fun={funItem} />;
                    })
                }
            </div>
        );
    }
}

export default FunList;