import React from 'react';
import { BrowserRouter as Router, Route,} from "react-router-dom";

import Header from './HeaderComponents/Header';
import Menu from './MenuComponents/Menu';
import Fun from './funComponent/Fun';
import Learn from './learnComponent/Learn';
import Schedule from './scheduleComponents/Schedule';
import Skeleton from './learnComponent/skillPagesComponents/Skeleton';
import Headings from './learnComponent/skillPagesComponents/Headings';
import Images from './learnComponent/skillPagesComponents/Images';
import Tags from './learnComponent/skillPagesComponents/Tags';

class Dashboard extends React.Component {
    render() {
        return (
            <Router>
              <Header />
              <div className="App">
                <Menu />
                <Route exact path="/schedule" component={Schedule} />
                <Route exact path="/learn" component={Learn} />
                <Route exact path="/fun" component={Fun} />
                <Route exact path="/htmlSkeleton" component={Skeleton} />
                <Route exact path="/htmlHeadings" component={Headings} />
                <Route exact path="/htmlImages" component={Images} />
                <Route exact path="/cssTags" component={Tags} />
              </div>
            </Router>
          );
        }
}

export default Dashboard; 