import React from 'react';
import InputField from './InputField';
import SubmitButton from './SubmitButton';
import UserStore from '../../../stores/UserStore';
import './Login.css';

class RegisterForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            first_name: "",
            last_name: "",
            username: "",
            email: "",
            password: "",
            isRegestered: false,
            buttonDisabled: false
        }
    }

    setInputValue(property, val) {
        val = val.trim();
        this.setState({
            [property]: val
        })
    }

    resetForm() {
        this.setState({
            first_name: "",
            last_name: "",
            username: "",
            email: "",
            password: "",
            buttonDisabled: false
        })
    }

    async doRegister() {
        if(!this.state.first_name || !this.state.last_name){
            return;
        }
        if(!this.state.username) {
            return;
        }
        if(!this.state.email){
            return;
        }
        if(!this.state.password) {
            return;
        }
        this.setState({
            buttonDisabled: true
        })

        try {
            let res = await fetch('/register', {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    "Content-Type": 'application/json'
                },
                body: JSON.stringify({
                    first_name: this.state.first_name,
                    last_name: this.state.last_name,
                    username: this.state.username,
                    email: this.state.email,
                    password: this.state.password
                })
            });

            let result = await res.json();
            if( result && result.success) {
                UserStore.isRegistered = true;
                this.setState({
                    isRegestered: true
                })
            }

            else if (result && result.success === false) {
                this.resetForm();
                alert(result.msg);
            }
            
        }

        catch(e) {
            console.log(e);
            this.resetForm();
        }
    }

  render() {

     return (
        <div className="loginForm">
            Signup
            <InputField type="text"
            placeholder="First name"
            value={this.state.first_name ? this.state.first_name : '' }
            onChange={ (val) => this.setInputValue('first_name', val)}
            />

            <InputField type="text"
            placeholder="Last name"
            value={this.state.last_name ? this.state.last_name : '' }
            onChange={ (val) => this.setInputValue('last_name', val)}
            />

            <InputField type="email"
            placeholder="Email"
            value={this.state.email ? this.state.email : '' }
            onChange={ (val) => this.setInputValue('email', val)}
            />

            <InputField type="text"
            placeholder="username"
            value={this.state.username ? this.state.username : '' }
            onChange={ (val) => this.setInputValue('username', val)}
            />

            <InputField type="text"
            placeholder="password"
            value={this.state.password ? this.state.password : '' }
            onChange={ (val) => this.setInputValue('password', val)}
            />

            <SubmitButton text="Signup"
                disabled={this.state.buttonDisabled}
                onClick={ () => this.doRegister() }
            />
        </div>
  );
  }
}

export default RegisterForm;