import React from 'react';
import { BrowserRouter as Router, Route,} from "react-router-dom";
import  './Assets/css/style.css';

import Landingpage from './components/LandingpageCompontents/Landingpage';
import Signin from './components/SigninSignupComponents/Signin';
import Signup from './components/SigninSignupComponents/Signup';
import Dashboard from './components/DashboardComponents/Dashboard';

class App extends React.Component {
  render() {
    return (
      <Router >
        <div className="App">
        <Route strict exact path="/" component={Landingpage} />
        <Route strict exact path="/signin" component={Signin} />
        <Route strict exact path="/signup" component={Signup} />
        <Route strict exact path="/dashboard" component={Dashboard} />
        </div>
      </Router>
    );
  }
}

export default App;
